import React, { useState } from 'react';
import { BarChart, Bar, XAxis, YAxis, Tooltip, Legend, CartesianGrid } from 'recharts';

function FinanceManager() {
  const [income, setIncome] = useState(0);
  const [expense, setExpense] = useState(0);
  const [salary, setSalary] = useState(0);
  const [bonus, setBonus] = useState(0);
  const [netIncome, setNetIncome] = useState(0);

  const handleCalculate = () => {
    const incomeValue = parseFloat(income) || 0;
    const expenseValue = parseFloat(expense) || 0;
    const salaryValue = parseFloat(salary) || 0;

    const calculatedBonus = salaryValue * 0.1; // 假設獎金是薪資的 10%
    const calculatedNetIncome = incomeValue - expenseValue + calculatedBonus;

    setBonus(calculatedBonus);
    setNetIncome(calculatedNetIncome);
  };

  const data = [
    { name: '收入', value: parseFloat(income) || 0 },
    { name: '支出', value: parseFloat(expense) || 0 },
    { name: '淨收入', value: netIncome }
  ];

  return (
    <div>
      <h2>財務管理系統</h2>
      
      <div>
        <label>
          收入:
          <input type="number" placeholder="收入" onChange={(e) => setIncome(e.target.value)} />
        </label>
      </div>
      
      <div>
        <label>
          支出:
          <input type="number" placeholder="支出" onChange={(e) => setExpense(e.target.value)} />
        </label>
      </div>
      
      <div>
        <label>
          薪資:
          <input type="number" placeholder="薪資" onChange={(e) => setSalary(e.target.value)} />
        </label>
      </div>
      
      <button onClick={handleCalculate}>計算</button>

      <div>
        <h3>計算結果</h3>
        <p>獎金: {bonus.toFixed(2)}</p>
        <p>淨收入: {netIncome.toFixed(2)}</p>
      </div>

      <BarChart width={500} height={300} data={data}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="name" />
        <YAxis />
        <Tooltip />
        <Legend />
        <Bar dataKey="value" fill="#8884d8" />
      </BarChart>
    </div>
  );
}

export default FinanceManager;
